  .area_box{
    //border: 1px solid black;
    cursor: text;
    overflow: hidden;
    textarea{
      height: 100%;
      width: 100%;
      border: 0;
      resize: none;
      transform: scale(1);
      font-size: 14px;
      font-family: monospace;
      color: #000;
      overflow: hidden;
    }
  }
}
